.card {
    background: #fff;
    background-size:cover;
    border:5px solid #f1f1f1;;
    border-radius: 3px;
    height: 170px;
    margin: 1rem;
    position: relative;
    width: 170px;
    -webkit-box-shadow: 0 3px 6px rgb(153, 153, 153), 0 3px 6px rgb(153, 153, 153);
            box-shadow: 0 3px 6px rgb(153, 153, 153), 0 3px 6px rgb(153, 153, 153);
    text-align: left;
    
    
}

.card > .img-container {
  height: 100%;
  background-size:cover;
  background-position:50%;
  /* overflow: hidden; */
  /* text-align: center; */
  /* background:#6CADDC; */
}

.card > .img-container > img {
  max-width: 100%;
  max-height: 100%;
}

.card:hover {
  -webkit-box-shadow: 11px 11px 25px rgb(37, 211, 130);
          box-shadow: 11px 11px 25px rgb(37, 211, 130);
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}
  /* Shake */

.shake{
  -webkit-animation-name: shake;
          animation-name: shake;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: initial;
          animation-timing-function: initial;
  -webkit-animation-delay: 0.0125s;
          animation-delay: 0.0125s;
  -webkit-animation-iteration-count: initial;
          animation-iteration-count: initial;
  -webkit-animation-direction: initial;
          animation-direction: initial;
  -webkit-animation-fill-mode: initial;
          animation-fill-mode: initial;
  -webkit-animation-play-state: initial;
          animation-play-state: initial;
}
  


